@import url("https://fonts.googleapis.com/css2?family=Saira:wdth,wght@50..125,200..800&display=swap");

body,
* {
  font-family: "Saira", sans-serif;
  font-weight: 800;
}

p {
  font-weight: 200;
}

h1,
h2,
h3,
p {
  margin: 0;
}

h2 {
  font-size: 40px;
  letter-spacing: 1px;
  line-height: 40px;
  text-transform: uppercase;
}

h3 {
  font-size: 20px;
  letter-spacing: 10px;
  line-height: 20px;
}
